import React from 'react'
import { Layout, Stack, Hero, Main, PreFooter, Sidebar } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Navigation from '@components/Navigation'
import Seo from '@widgets/Seo'
import Categories from '@widgets/Categories'
import Sticky from '@components/Sticky'
import BannerVertical from '@widgets/BannerVertical'
import attachSocialIcons from '@helpers/attachSocialIcons'
import useSiteMetadata from '@helpers-blog/useSiteMetadata'
import { useBlogCategories } from '@helpers-blog'

const ThreePosts = ({posts, category}) => {
  const group = posts.group.filter((p) => p.categoryName === category)[0]


  return (
              <Stack
                title={group.categoryName}
                titleLink={group.nodes[0].category.slug}
                direction={['column', 'column', 'column', 'row']}
              >
                <Main
                  sx={{
                    pl: 0,
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Main>
                <Main
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <Divider space={2} />
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Main>
                <Main
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <Divider space={2} />
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={2}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Main>
              </Stack>
  )
}


const FivePosts = ({posts, category}) => {
  const group = posts.group.filter((p) => p.categoryName === category)[0]

  return (
              <Stack
                title={group.categoryName}
                titleLink={group.nodes[0].category.slug}
                direction={['column', 'column', 'column', 'row']}
              >
                <Main
                  sx={{
                    pl: 0,
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Main>
                  <Divider space={2} />
                <Main
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Main>
                <Main
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <Divider space={2} />
                  <CardList
                    nodes={group.nodes}
                    limit={2}
                    skip={2}
                    columns={[1, 1, 3, 1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal-aside',
                    ]}
                    omitCategory
                  />
                  <Divider space={2} />
                </Main>
              </Stack>

  )
}


const Posts = ({
  data: { posts = {}, featuredPosts = {}, paginatedPosts = {}, recentPosts = {} },
  ...props
}) => {
  const { pageContext: { services = {}, basePath } = {} } = props
  const { social } = useSiteMetadata()
  const categories = useBlogCategories()

  // Present the sidebar links in the same order as the Hero links
  const sideBarPosts = ["/about/", "/wfindex/", "/dataindex/"].map(slug => { 
    return featuredPosts.nodes.find(obj => {return obj.slug === slug})
  })

  return (
    <Layout {...props}>
      <Seo title='Home' />

      <Stack effectProps={{ effect: false }}>
          <Hero />
      </Stack>


      <Stack effectProps={{ effect: false }}>
        <Main>
          <Divider space={2} />
          <CardList
            nodes={recentPosts.nodes}
            limit={6}
            columns={[1, 2]}
            variant='horizontal-aside'
            loading='eager'
          />
        </Main>
        <Sidebar sx={{ pl: `3`, flexBasis: `1/4` }}>
          <BannerVertical />
        </Sidebar>
      </Stack>

      <Divider space={4} />

      <Stack effectProps={{ effect: false }}>
      <Main>

      <ThreePosts posts={posts} category="Articles" />
      <Divider space={2} />
      <FivePosts posts={posts} category="How Tos" />
      <Divider space={2} />
      <ThreePosts posts={posts} category="Software Releases" />
      <Divider space={2} />
      <FivePosts posts={posts} category="Data Releases" />

      </Main>
        <Sidebar>
          <Sticky>
          <Categories categories={categories} title=""/>
          <Divider space={1} />
          <Divider space={1} />
          <Divider space={1} />
          <CardList
            nodes={sideBarPosts}
            limit={6}
            columns={[1, 1, 1]}
            variant='horizontal-aside'
            loading='eager'
            omitFooter
          />
          <Divider space={1} />
            <Navigation items={attachSocialIcons(social)} iconOnly />
          </Sticky>
        </Sidebar>
      </Stack> 
      
      <Divider space={2} />

      <PreFooter></PreFooter>
    </Layout>
  )
}

export default Posts
