import React from 'react'
import { Link } from 'gatsby'
import { Flex, Button, Heading, Text } from 'theme-ui'
import { FaUserFriends } from 'react-icons/fa'

const styles = {
  wrapper: {
    alignItems: `center`,
    flexDirection: `column`,
    bg: `omegaLighter`,
    borderRadius: `lg`,
    size: `full`,
    p: 4,
  },
  heading: {
    color: `omegaDark`,
    wordSpacing: 500, //force line breaks
    svg: {
      color: `beta`,
      size: `icon.lg`,
      display: `block`,
      mb: 3,
    },
  },
  subheading: {
    color: `omegaDark`,
    fontWeight: `normal`,
  },
  list: {
    color: `omegaDark`,
    listStyle: `none`,
    m: 0,
    p: 0,
    li: {
      p: 0,
      my: 2,
    },
    'li:before': {
      content: `""`,
      display: `inline-block`,
      width: `icon.xs`,
      bg: `success`,
      borderRadius: `full`,
      size: `7px`,
      mr: `7px`,
    },
  },
  button: {
    display: `block`,
    mt: `auto`,
  },
}

const BannerVertical = () => (
  <Flex sx={styles.wrapper}>
    <Heading variant='h2' sx={styles.heading}>
      <FaUserFriends />
    </Heading>
    <Heading variant='h4' sx={styles.subheading}>
      EPI2ME provides best practice bioinformatics analyses for nanopore sequencing
    </Heading>
    <Text as='ul' variant='small' sx={styles.list}>
      <li>Simply and easily analyse your sequencing data</li>
      <li>Transparent: all our analyses are open source</li>
      <li>No need to upload your data to the cloud</li>
    </Text>
    <Button
      variant='primary'
      as={Link}
      to='/downloads'
      sx={styles.button}
      aria-label='Download Now'
    >
      Download Now
    </Button>
  </Flex>
)

export default BannerVertical
